	module.exports = {"data":{"postsJson":{"title":"Calling, Healing & Teaching","questionsTitle":"COMMANDS & PROMISES","display":"block","bookName":"누가복음","ref":"5:1-6:49","html":"<p>   <strong>5</strong><u>1</u>무리가 몰려와서 하나님의 말씀을 들을새 예수는 게네사렛 호숫가에 서서 <u>2</u>호숫가에 배 두 척이 있는 것을 보시니 어부들은 배에서 나와서 그물을씻는지라 <u>3</u>예수께서 한 배에 오르시니 그 배는 시몬의 배라 육지에서 조금 떼기를 청하시고 앉으사 배에서 무리를 가르치시더니 <u>4</u>말씀을 마치시고 시몬에게 이르시되</p><h1>예수님</h1><h2>깊은 데로 가서 그물을 내려 고기를 잡으라</h2><p><u>5</u>시몬이 대답하여 이르되</p><h1>시몬 베드로</h1><h3>선생님우리들이밤이새도록수고하였으되잡은것이없지마는말씀에의지하여내가그물을내리리이다</h3><p>하고 <u>6</u>그렇게 하니 고기를 잡은 것이 심히 많아 그물이 찢어지는지라 <u>7</u>이에 다른 배에 있는 동무들에게 손짓하여 와서 도와 달라 하니 그들이 와서 두 배에 채우매 잠기게 되었더라<br />   <u>8</u>시몬 베드로가 이를 보고 예수의 무릎 아래에 엎드려 이르되</p><h1>시몬 베드로</h1><h3>주여나를떠나소서나는죄인이로소이다</h3><p>하니 <u>9</u>이는 자기 및자기와 함께 있는 모든 사람이 고기 잡힌것으로 말미암아 놀라고<u>10</u>세베대의 아들로서 시몬의 동업자인 야고보와 요한도 놀랐음이라<br />   예수께서 시몬에게 이르시되</p><h1>예수님</h1><h2>무서워하지 말라 이제 후로는 네가 사람을 취하리라</h2><p>하시니 <u>11</u>그들이 배들을 육지에 대고 모든 것을 버려 두고 예수를 따르니라<br />   <u>12</u>예수께서 한 동네에 계실 때에 온 몸에 나병 들린 사람이 있어 예수를 보고 엎드려 구하여 이르되</p><h1>나병 환자</h1><h4>주여 원하시면 나를 깨끗하게 하실 수 있나이다</h4><p>하니 <u>13</u>예수께서 손을 내밀어 그에게 대시며 이르시되</p><h1>예수님</h1><h2>내가 원하노니 깨끗함을 받으라</h2><p>하신대 나병이 곧 떠나니라 <u>14</u>예수께서 그를 경고하시되</p><h1>예수님</h1><h2>아무에게도 이르지 말고 가서 제사장에게 네 몸을 보이고 또 네가 깨끗하게 됨으로 인하여 모세가 명한 대로 예물을 드려 그들에게 입증하라</h2><p>하셨더니 <u>15</u>예수의 소문이 더욱 퍼지매 수많은 무리가 말씀도 듣고 자기 병도 고침을 받고자 하여 모여 오되 <u>16</u>예수는 물러가사 한적한 곳에서 기도하시니라<br />   <u>17</u>하루는 가르치실 때에 갈릴리의 각 마을과 유대와 예루살렘에서 온 바리새인과 율법교사들이 앉았는데 병을 고치는 주의 능력이 예수와 함께 하더라<br />   <u>18</u>한 중풍병자를 사람들이 침상에 메고 와서 예수 앞에 들여놓고자 하였으나 <u>19</u>무리 때문에 메고 들어갈 길을 얻지 못한지라 지붕에 올라가 기와를 벗기고 병자를 침상째 무리 가운데로 예수 앞에 달아 내리니 <u>20</u>예수께서 그들의 믿음을 보시고 이르시되</p><h1>예수님</h1><h2>이 사람아 네 죄 사함을 받았느니라</h2><p>하시니 <u>21</u>서기관과 바리새인들이 생각하여 이르되</p><h1>서기관과 바리새인들</h1><h4>이 신성 모독 하는 자가 누구냐 오직 하나님 외에 누가 능히 죄를 사하겠느냐</h4><p><u>22</u>예수께서 그 생각을 아시고 대답하여 이르시되</p><h1>예수님</h1><h2>너희 마음에 무슨 생각을 하느냐 <u>23</u>네 죄 사함을 받았느니라 하는 말과 일어나 걸어가라 하는 말이 어느 것이 쉽겠느냐 <u>24</u>그러나 인자가 땅에서 죄를 사하는 권세가 있는 줄을 너희로 알게 하리라</h2><p>하시고 중풍병자에게 말씀하시되</p><h1>예수님</h1><h2>내가 네게 이르노니 일어나 네 침상을 가지고 집으로 가라</h2><p>하시매 <u>25</u>그 사람이 그들 앞에서 곧 일어나 그 누웠던 것을 가지고 하나님께 영광을 돌리며 자기 집으로 돌아가니 <u>26</u>모든 사람이 놀라 하나님께 영광을 돌리며 심히 두려워하여 이르되</p><h1>군중들</h1><h4>오늘 우리가 놀라운 일을 보았다</h4><p>하니라<br />   <u>27</u>그 후에 예수께서 나가사 레위라 하는 세리가 세관에 앉아 있는 것을 보시고</p><h1>예수님</h1><h2>나를 따르라</h2><p>하시니 <u>28</u>그가 모든 것을 버리고 일어나 따르니라<br />   <u>29</u>레위가 예수를 위하여 자기 집에서 큰 잔치를 하니 세리와 다른 사람이 많이 함께 앉아 있는지라 <u>30</u>바리새인과 그들의 서기관들이 그 제자들을 비방하여 이르되</p><h1>서기관과 바리새인들</h1><h4>너희가 어찌하여 세리와 죄인과 함께 먹고 마시느냐</h4><p><u>31</u>예수께서 대답하여 이르시되</p><h1>예수님</h1><h2>건강한 자에게는 의사가 쓸 데 없고 병든 자에게라야 쓸 데 있나니 <u>32</u>내가 의인을 부르러 온 것이 아니요 죄인을 불러 회개시키러 왔노라</h2><p><u>33</u>그들이 예수께 말하되</p><h1>서기관과 바리새인들</h1><h4>요한의 제자는 자주 금식하며 기도하고 바리새인의 제자들도 또한 그리하되 당신의 제자들은 먹고 마시나이다</h4><p><u>34</u>예수께서 그들에게 이르시되</p><h1>예수님</h1><h2>혼인 집 손님들이 신랑과 함께 있을 때에 너희가 그 손님으로 금식하게 할 수 있느냐 <u>35</u>그러나 그 날에 이르러 그들이 신랑을 빼앗기리니 그 날에는 금식할 것이니라</h2><p><u>36</u>또 비유하여 이르시되</p><h1>예수님</h1><h2>새 옷에서 한 조각을 찢어 낡은 옷에 붙이는 자가 없나니 만일 그렇게 하면 새 옷을 찢을 뿐이요 또 새 옷에서 찢은 조각이 낡은 것에 어울리지 아니하리라<br />   <u>37</u>새 포도주를 낡은 가죽 부대에 넣는 자가 없나니 만일 그렇게 하면 새 포도주가 부대를 터뜨려 포도주가 쏟아지고 부대도 못쓰게 되리라 <u>38</u>새 포도주는 새 부대에 넣어야 할 것이니라 <u>39</u>묵은 포도주를 마시고 새 것을 원하는 자가 없나니 이는 묵은 것이 좋다 함이니라<br />   <strong>6</strong><u>1</u>안식일에 예수께서 밀밭 사이로 지나가실새 제자들이 이삭을 잘라 손으로 비비어 먹으니 <u>2</u>어떤 바리새인들이 말하되</p><h1>바리새인들</h1><h4>어찌하여 안식일에 하지 못할 일을 하느냐</h4><p><u>3</u>예수께서 대답하여 이르시되</p><h1>예수님</h1><h2>다윗이 자기 및 자기와 함께 한 자들이 시장할 때에 한 일을 읽지 못하였느냐 <u>4</u>그가 하나님의 전에 들어가서 다만 제사장 외에는 먹어서는 안 되는 진설병을 먹고 함께 한 자들에게도 주지 아니하였느냐</h2><p><u>5</u>또 이르시되</p><h1>예수님</h1><h2>인자는 안식일의 주인이니라</h2><p>하시더라<br />   <u>6</u>또 다른 안식일에 예수께서 회당에 들어가사 가르치실새 거기 오른손 마른 사람이 있는지라 <u>7</u>서기관과 바리새인들이 예수를 고발할 증거를 찾으려 하여 안식일에 병을 고치시는가 엿보니 <u>8</u>예수께서 그들의 생각을 아시고 손 마른 사람에게 이르시되</p><h1>예수님</h1><h2>일어나 한가운데 서라</h2><p>하시니 그가 일어나 서거늘 <u>9</u>예수께서 그들에게 이르시되</p><h1>예수님</h1><h2>내가 너희에게 묻노니 안식일에 선을 행하는 것과 악을 행하는 것, 생명을 구하는 것과 죽이는 것, 어느 것이 옳으냐</h2><p>하시며 <u>10</u>무리를 둘러보시고 그 사람에게 이르시되</p><h1>예수님</h1><h2>네 손을 내밀라</h2><p>하시니 그가 그리하매 그 손이 회복된지라 <u>11</u>그들은 노기가 가득하여 예수를 어떻게 할까 하고 서로 의논하니라<br />   <u>12</u>이 때에 예수께서 기도하시러 산으로 가사 밤이 새도록 하나님께 기도하시고 <u>13</u>밝으매 그 제자들을 부르사 그 중에서 열둘을 택하여 사도라 칭하셨으니<br />   <u>14</u>곧 베드로라고도 이름을 주신 시몬과<br />   그의 동생 안드레와<br />   야고보와<br />   요한과<br />   빌립과<br />   바돌로매와<br />   <u>15</u>마태와<br />   도마와<br />   알패오의 아들 야고보와<br />   셀롯이라는 시몬과<br />   <u>16</u>야고보의 아들 유다와<br />   예수를 파는 자 될 가룟 유다라<br />   <u>17</u>예수께서 그들과 함께 내려오사 평지에 서시니 그 제자의 많은 무리와 예수의 말씀도 듣고 병 고침을 받으려고 유대 사방과 예루살렘과 두로와 시돈의 해안으로부터 온 많은 백성도 있더라 <u>18</u>더러운 귀신에게 고난 받는 자들도 고침을 받은지라<u>19</u>온 무리가 예수를 만지려고 힘쓰니 이는 능력이 예수께로부터 나와서 모든 사람을 낫게 함이러라<br />   <u>20</u>예수께서 눈을 들어 제자들을 보시고 이르시되</p><h1>예수님</h1><h2>너희 가난한 자는 복이 있나니<br />   하나님의 나라가 너희 것임이요<br />   <u>21</u>지금 주린 자는 복이 있나니<br />   너희가 배부름을 얻을 것임이요<br />   지금 우는 자는 복이 있나니<br />   너희가 웃을 것임이요<br />   <u>22</u>인자로 말미암아 사람들이 너희를 미워하며 멀리하고 욕하고 너희 이름을 악하다 하여 버릴 때에는 너희에게 복이 있도다 <u>23</u>그 날에 기뻐하고 뛰놀라 하늘에서 너희 상이 큼이라 그들의 조상들이 선지자들에게 이와 같이 하였느니라<br />   <u>24</u>그러나 화 있을진저 너희 부요한 자여<br />   너희는 너희의 위로를 이미 받았도다<br />   <u>25</u>화 있을진저 너희 지금 배부른 자여<br />   너희는 주리리로다<br />   화 있을진저 너희 지금 웃는 자여<br />   너희가 애통하며 울리로다<br />   <u>26</u>모든 사람이 너희를 칭찬하면 화가 있도다<br />   그들의 조상들이 거짓 선지자들에게 이와 같이 하였느니라<br />   <u>27</u>그러나 너희 듣는 자에게 내가 이르노니 너희 원수를 사랑하며 너희를 미워하는 자를 선대하며 <u>28</u>너희를 저주하는 자를 위하여 축복하며 너희를 모욕하는 자를 위하여 기도하라 <u>29</u>너의 이 뺨을 치는 자에게 저 뺨도 돌려대며 네 겉옷을 빼앗는 자에게 속옷도 거절하지 말라 <u>30</u>네게 구하는 자에게 주며 네 것을 가져가는 자에게 다시 달라 하지 말며 <u>31</u>남에게 대접을 받고자 하는 대로 너희도 남을 대접하라<br />   <u>32</u>너희가 만일 너희를 사랑하는 자만을 사랑하면 칭찬 받을 것이 무엇이냐 죄인들도 사랑하는 자는 사랑하느니라 <u>33</u>너희가 만일 선대하는 자만을 선대하면 칭찬 받을 것이 무엇이냐 죄인들도 이렇게 하느니라 <u>34</u>너희가 받기를 바라고 사람들에게 꾸어 주면 칭찬 받을 것이 무엇이냐 죄인들도 그만큼 받고자 하여 죄인에게 꾸어 주느니라<br />   <u>35</u>오직 너희는 원수를 사랑하고 선대하며 아무 것도 바라지 말고 꾸어 주라 그리하면 너희 상이 클 것이요 또 지극히 높으신 이의 아들이 되리니 그는 은혜를 모르는 자와 악한 자에게도 인자하시니라 <u>36</u>너희 아버지의 자비로우심 같이 너희도 자비로운 자가 되라<br />   <u>37</u>비판하지 말라 그리하면 너희가 비판을 받지 않을 것이요 정죄하지 말라 그리하면 너희가 정죄를 받지 않을 것이요 용서하라 그리하면 너희가 용서를 받을 것이요 <u>38</u>주라 그리하면 너희에게 줄 것이니 곧 후히 되어 누르고 흔들어 넘치도록 하여 너희에게 안겨 주리라 너희가 헤아리는 그 헤아림으로 너희도 헤아림을 도로 받을 것이니라</h2><p><u>39</u>또 비유로 말씀하시되</p><h1>예수님</h1><h2>맹인이 맹인을 인도할 수 있느냐 둘이 다 구덩이에 빠지지 아니하겠느냐 <u>40</u>제자가 그 선생보다 높지 못하나 무릇 온전하게 된 자는 그 선생과 같으리라<br />   <u>41</u>어찌하여 형제의 눈 속에 있는 티는 보고 네 눈 속에 있는 들보는 깨닫지 못하느냐 <u>42</u>너는 네 눈 속에 있는 들보를 보지 못하면서 어찌하여 형제에게 말하기를 형제여 나로 네 눈 속에 있는 티를 빼게 하라 할 수 있느냐 외식하는 자여 먼저 네 눈 속에서 들보를 빼라 그 후에야 네가 밝히 보고 형제의 눈 속에 있는 티를 빼리라<br />   <u>43</u>못된 열매 맺는 좋은 나무가 없고 또 좋은 열매 맺는 못된 나무가 없느니라 <u>44</u>나무는 각각 그 열매로 아나니 가시나무에서 무화과를, 또는 찔레에서 포도를 따지 못하느니라 <u>45</u>선한 사람은 마음에 쌓은 선에서 선을 내고 악한 자는 그 쌓은 악에서 악을 내나니 이는 마음에 가득한 것을 입으로 말함이니라<br />   <u>46</u>너희는 나를 불러 주여 주여 하면서도 어찌하여 내가 말하는 것을 행하지 아니하느냐 <u>47</u>내게 나아와 내 말을 듣고 행하는 자마다 누구와 같은 것을 너희에게 보이리라 <u>48</u>집을 짓되 깊이 파고 주추를 반석 위에 놓은 사람과 같으니 큰 물이 나서 탁류가 그 집에 부딪치되 잘 지었기 때문에 능히 요동하지 못하게 하였거니와 <u>49</u>듣고 행하지 아니하는 자는 주추 없이 흙 위에 집 지은 사람과 같으니 탁류가 부딪치매 집이 곧 무너져 파괴됨이 심하니라</h2><p>하시니라</p>","path":"/328","Question1":"What God-given instructions do you note in this portion of Scripture?","Question2":"Put yourself in the original setting and creatively think about what it might have been like to receive this instruction. How does this relate to you where you live and work today?","Question3":"What God-inspired hope do you note in this portion of Scripture? How can you grow to have more of this in your own life?","Question4":"Commit yourself to tell a friend about what this hope means for you today.","prevPath":"/327","nextPath":"/329"}},"pathContext":{}}

